@import url(https://fonts.googleapis.com/css?family=Roboto|Roboto+Slab);
body {
  background-color: #f8f0f1;
  color: #553b42;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

button {
  background-color: #aa19c8;
  border: none;
  color: #f8f0f1;
  display: inline-block;
  font-weight: bold;
  padding: .3em;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

div { font-family: 'Roboto', sans-serif; }

form {
  background-color: #f8f0f1;
  border: 2px solid #aa19c8;
  box-sizing: border-box;
  box-shadow: 3px 3px 9px #aa19c8;
  margin: 5em auto;
  max-width: 500px;
  overflow: hidden;
  padding: 2em;
}

h2 {
  color: #aa19c8;
  font-family: 'Roboto Slab', serif;
  font-size: 1.1em;
}

h3 {
  color: #031a68;
  font-family: 'Roboto Slab', serif;
  font-size: 1.1em;
}

h4 { color: #031a68; }

#overlay {
  background-color: white;
  bottom: 0;
  height: 100%;
  opacity: .8;
  position: absolute;
  top: 0;
  width: 100%;
}

#page { margin: 1em; }

.blue { color: #031a68; }
.blurred { opacity: .2; }
.bold { font-weight: bold; }
.box-item-list-style li {
  border: 2px solid #ddd;
  margin: .5em;
  padding: .5em;

}
.cap { text-transform: capitalize; }
.center {
  display: block;
  margin: 0 auto;
  text-align: center;
}
.flex {
  border-left: 2px solid #ddd;
  display: flex;
  margin-top: 1em;
}
.flex .col {
  border-right: 2px solid #ddd;
  color: #553b42;
}
.flex .col.highlight-1:not(:first-child) h3 { opacity: .2; }
.font-l { font-size: 14px; }
.inl-b { display: inline-block; }
.input-min-w { min-width: 100px; }
.left { float: left; }
.marg-b { margin-bottom: 1.5em; }
.marg-r { margin-right: .5em; }
.marg-ver {
  margin-bottom: 1.5em;
  margin-top: 1.5em;
}
.no-list-style {
  list-style: none;
  margin: 0;
  padding-left: 0;
}
.opaque { opacity: 1; }
.overflow-hide { overflow: hidden; }
.overlay {
  bottom: 0;
  margin: 5%;
  position: absolute;
  top: 0;
  width: 90%;
}
.padding-hor { padding-left: .2em; padding-right: .2em; }
.right { float: right; }
.right-border { border-right: 3px solid #031a68; }
.serif { font-family: 'Roboto Slab', serif; }
.upperc { text-transform: uppercase; }
::-webkit-input-placeholder {
  color: red;
  font-weight: bold;
}
::-ms-input-placeholder {
  color: red;
  font-weight: bold;
}
::placeholder {
  color: red;
  font-weight: bold;
}

@media (min-width: 1000px) {
  html{ font-size: 16px; }
}

@media (min-width: 800px) and (max-width: 1000px){
  html{ font-size: 16px; }
}

@media (max-width: 800px){
  html{ font-size: 14px; }
}


.App-UpComingWeek { margin: 2em 0 ; }

@media (min-width: 1000px) {
  .table-7-4-2 .col { flex-grow: 1; }
}

@media (min-width: 800px) and (max-width: 1000px){
  .table-7-4-2 { flex-wrap: wrap; }
  .table-7-4-2 .col {
    box-sizing: border-box;
    width: 25%;
  }
}

@media (max-width: 800px){
  .table-7-4-2 { flex-wrap: wrap; }
  .table-7-4-2 .col {
    box-sizing: border-box;
    width: 50%;
  }
}

.circle {
  display: inline-block;
  font-weight: bold;
  font-size: 12px;
  height: 14px;
  padding: 5px;
  text-align: center;
  width: 14px;
}

.box {
  border: solid 1px;
  border-radius: 50%;
  color: green;
  margin: 2.5px;
}

.no-box {
  margin: 3.5px;
}

.title-circle { 
  margin-top: 6px; 
  width: 120px;
}

@media (min-width: 800px) {
  .table-4-2-1 { width: 50%; }
  .table-4-2-1 .col { flex-grow: 1; }
}

@media (max-width: 800px){
  .table-4-2-1 .col {
    box-sizing: border-box;
    width: 50%;
  }
}

